






































import { Component, Vue } from "vue-property-decorator";
import { UserModule } from "../store/modules/user";
import { Form as ElForm } from "element-ui";
import { defaultMountApp } from "../setting";
@Component({
  components: {}
})
export default class Login extends Vue {
  loading: boolean = false;
  ruleForm: any = {
    username: "admin",
    password: "123456"
  };
  rules: any = {
    username: [{ required: true, message: "请输入账号", trigger: "blur" }, {}],
    password: [
      { required: true, message: "请输入密码", trigger: "blur" },
      { min: 6, max: 30, message: "长度在最少6位", trigger: "change" }
    ]
  };
  private submitForm() {
    // that.$refs[formName].validate((valid: any)
    (this.$refs.ruleForm as ElForm).validate(async (valid: boolean) => {
      if (valid) {
        this.loading = true;
        try {
          const res = await UserModule.Login(
            this.ruleForm.username,
            this.ruleForm.password
          );
          console.log(res);

          this.loading = false;
          this.$router.push({
            path: defaultMountApp
          });
        } catch (error) {
          this.loading = false;
        }

        // setTimeout(() => {
        //   this.loading = false;
        //   this.$router.push({
        //     path: "/app/"
        //   });
        // }, 500);
      } else {
        console.log("error submit!!");
        return false;
      }
    });
  }
  resetForm(formName: any) {
    const that: any = this;
    that.$refs[formName].resetFields();
  }
}
